import { h, Component } from 'preact';
import style from './style.css';
import Textfield from '../../components/form/textfield';
import Button from '../../components/form/button';
import Checkbox from '../../components/form/checkbox';

export default class CreateActivity extends Component {
	constructor() {
		super();
		this.state = {
			name: '',
			email: '',
			description: '',
			photoLink: '',
			address: '',
			contacted: false
		};
	}
	nameChange = name => this.setState({ name });
	emailChange = email => this.setState({ email });
	descriptionChange = description => this.setState({ description });
	photoLinkChange = photoLink => this.setState({ photoLink });
	addressChange = address => this.setState({ address });
	contactedChange = checked => this.setState({ contacted: checked });

	clickEvent = () => console.log(this.state);

	render() {
		return (
			<div class={style.home}>
				<div class={style.headerHolder}>
					<h1>Create activity</h1>
					<Textfield
						placeholder="Name"
						value={this.state.name}
						onChange={this.nameChange}
					/>
					<Textfield
						placeholder="Email"
						value={this.state.email}
						onChange={this.emailChange}
					/>
					<Textfield
						placeholder="Description"
						value={this.state.description}
						onChange={this.descriptionChange}
					/>
					<Textfield
						placeholder="Photo link"
						value={this.state.photoLink}
						onChange={this.photoLinkChange}
					/>
					<Textfield
						placeholder="Address"
						value={this.state.address}
						onChange={this.addressChange}
					/>
					<Checkbox
						checked={this.state.contacted}
						onChange={this.contactedChange}
						title="Contacted"
					/>
					<Button onClick={this.clickEvent} title="Create" />
				</div>
			</div>
		);
	}
}



